.price {
    margin: 140px 0;

    p {
        font-size: 14px;
        line-height: 29px;
        margin: 20px 0;
        text-align: center;
        color: $advantages-gray;
    }
}

.price__divider {
    @include divider($advantages-gray);
}

.price__title {
    color: $gray;
    font-size: 36px;
    letter-spacing: 3.6px;
    font-family: "Source Sans Pro";
    font-weight: 700;
    text-transform: uppercase;
}

.price__block {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 40px;
}

.price__element {
    flex-basis: 32.4%;
    margin-top: 20px;
    background-color: $white-gray;

    &:hover {
        .price__top {
            background-color: $red;
        }

        .price__bottom {
            .price__number {
                p {
                    color: $red;
                }
            }
        }
    }
}

.price__top {
    background-color: $advantages-gray;
    padding-bottom: 90px;
    transition-duration: 500ms;
    transition-property: background-color;

    .price__name {
        font-size: 24px;
        font-weight: 600;
        letter-spacing: 1.8px;
        text-transform: uppercase;
        color: $white;
        margin: 0;
        padding: 40px 0;
    }
}

.price__bottom {
    padding-bottom: 20px;
}

.price__number {
    width: 153px;
    background-color: $white;
    border: 10px solid rgba(244, 245, 249, 0.25);
    border-radius: 50%;
    margin: -86px auto 0;
    padding: 40px 0;

    p {
        font-size: 39px;
        font-family: "Source Sans Pro";
        font-weight: 700;
        line-height: 40px;
        margin: 0;
        color: $advantages-gray;
        transition-duration: 500ms;
        transition-property: color;

        span {
            display: block;
            font-size: 13px;
            line-height: 21px;
            font-weight: 400;
            color: $advantages-gray;
        }
    }
}

.price__list {
    list-style: none;
    padding: 0;
    text-align: center;
    margin: 40px 0;

    li {
        font-size: 14px;
        line-height: 20px;
        margin: 20px 0;
        font-weight: 600;
        color: $advantages-gray;
    }
}

.price__btn {
    @include btn($advantages-gray) {
        width: 145px;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.12px;
        padding: 13px 0;
        display: block;
        margin: 30px auto;
        font-weight: 600;
    }
}

.price__element--red {
    .price__top {
        background-color: $red;
    }

    .price__number {
        p {
            color: $red;
        }
    }

    .price__btn {
        background-color: $red;
        border-color: $red;
    }
}

@media (max-width: 980px) {
    .price {
        margin: 50px 0;
    }
}

@media (max-width: 768px) {
    .price__block {
        justify-content: center;
    }

    .price__element {
        flex-basis: 45%;
        margin: 20px 2.5%;
    }
}

@media (max-width: 580px) {
    .price__element {
        flex-basis: 100%;
        margin: 15px 0;
    }
}
