.header {
    background: url("../img/section-top-bg.jpg") top center no-repeat;
    background-size: cover;
    position: relative;
    clip-path: polygon(0 0, 100% 0, 100% 80%, 50% 90%, 0 80%);

    &::after {
        content: "";
        background-color: rgba(0, 0, 0, 0.7);
        display: block;
        position: absolute;
        z-index: 1;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }
}

.header__top {
    position: relative;
    z-index: 2;
    padding: 40px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__menu {
    flex-basis: 62.5%;
}

.header__info {
    padding: 180px 0 300px;
    text-align: center;
    position: relative;
    z-index: 2;

    h1 {
        font-size: 24px;
        letter-spacing: 1.2px;
        color: $white-gray;
        font-family: "Source Sans Pro";

        span {
            font-size: 72px;
            font-weight: 700;
            font-family: "Open Sans";
            text-transform: uppercase;
            letter-spacing: 7.2px;
            margin: 30px 0 20px;
            display: block;
        }
    }

    > p {
        max-width: 600px;
        margin: 20px auto;
        font-size: 14px;
        line-height: 24px;
        color: $gray-light;
    }
}

.header__bottom {
    position: relative;
}

.header__icon {
    width: 64px;
    height: 64px;
    border: 5px solid rgba(0, 0, 0, 0.15);
    position: absolute;
    left: 50%;
    bottom: 8%;
    transform: translate(-50%, -64px);
    z-index: 2;
    border-radius: 50%;
    background-color: $white-gray;
    z-index: 1000;

    &:hover {
        .header__img {
            animation-name: rotate;
            animation-duration: 2s;
            animation-iteration-count: infinite;
        }
    }

    .header__img {
        width: 30px;
        height: 30px;
        fill: $red;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
}

@media (max-width: 980px) {
    .header__info {
        padding-top: 50px;

        h1 {
            span {
                font-size: 42px;
            }
        }
    }
}

@media (max-width: 768px) {
    .header__top {
        flex-wrap: wrap;
    }

    .header__left {
        flex-basis: 100%;
        text-align: center;
        margin-bottom: 30px;
    }

    .header__menu {
        flex-basis: 100%;
    }

    .header__info {
        > p {
            max-width: 100%;
        }
    }
}
